.feature{
    height: 90vh;
    
    position: relative;
    
}
.title{
    
        width: 100%;
        height: 100%;
        object-fit: cover;
    
}
.name{
    width: 35%;
    color: white;
    
}
.button{
    margin-left: 8px;
    
}
 .bt1 .bt2{
    border: none;
    border-radius: 5px;
    
}
.name h1{
    font-size: 125px;
    font-weight: bold;
    margin: 0 0 0 0;
}
.button{
    display: flex;
    
    
}
.bt1{

    font-size: 20px;
    border-radius: 5px;
    border: 1px solid white;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: center;    
}
.bt2{
    padding: 6px;
    font-size: 20px;
    background-color:  rgba(255, 255, 255, 0.306);
    color: white;
    border: 1px solid rgba(255, 255, 255, 0.306);
    border-radius: 5px;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}